import { ValidationOptions, registerDecorator } from "class-validator";
import { ColumnExistsRule } from "./column-exists.rule";

export function ColumnExists(model: any, validationOptions?: ValidationOptions) {
  return function (object: any, propertyName: string) {
    registerDecorator({
      name: 'ColumnExists',
      target: object.constructor,
      propertyName: propertyName,
      constraints: [model],
      options: validationOptions,
      validator: ColumnExistsRule,
    });
  };
}